.newsletter{
    margin-top: 5rem;
    background-color: $fondo--blanco--oscuro;
    border-radius: 10px;
    padding: 1rem;
    .newsletter--text{
        text-align: center;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .newsletter--input--button{
        margin-top: 1.5rem;
        display: flex;
        flex-direction: column;
    }
    button{
        cursor: pointer;
    }
}

@media (min-width:765px) {
    .newsletter{
        width: 60%;
        margin: 20rem auto;
        height: 50rem;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        .newsletter--text{
            h3{
                font-size: 3rem;
            }
        }
        .newsletter--input--button{
            width: 70%;
            margin: 0 auto;
        }
    }
}