.last--text{
    margin-top: 2rem;
    img{
        width: 25rem;
    }
    p{
        margin-bottom: 1rem;
    }
    .last--text--iconos{
        margin-top: 1rem;
        display: flex;
        gap: 1rem;
        i{
            cursor: pointer;
            color: $naranja;
            &:hover{
                color: $fondo--morado;
            }
        }
    }
}

@media (min-width: 765px){
    .last--text{
        width: 80%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        p{
            font-size: 2.5rem;
            text-align: center;
        }
    }
}