.front--div--img{
    position: relative;
    text-align: center;
    background-image: url('../img/front--div/mobile.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: calc(100vh - 7rem);
    .front--div--opacity{
        position: absolute;
        background-color: black;
        height: 100%;
        width: 100%;
        opacity: .7;
    }
    .front--div--textBox{
        position: absolute;
        h1, p{
            color: $color--fuente--blanca;
        }
        .textBox--h1{
            font-size: 3rem;
            span{
                font-size: 3rem;
                color: $naranja;
            }
        }
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .textBox--btn{
            button{
                width: 10rem;
                cursor: pointer;
            }
        }
    }
}
@media (min-width: 765px){
    .front--div--textBox{
        width: 100%;
        h1, p{
        font-size: 4rem;}
        
        .textBox--btn{
            button{
                font-size: 2rem;
                width: 25rem !important;
            }
        }
    }
}
