html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  color: #202020;
  background-color: #f0efef;
  padding: 1rem;
  margin: 0;
}

p {
  font-size: 1.5rem;
  margin: 0;
  font-weight: 400;
}

i {
  color: #2c2b5e;
}

a {
  text-decoration: none;
  color: #202020;
}

ul, li {
  text-decoration: none;
  padding: 0;
  list-style: none;
}

h1, h2, h3 {
  font-size: 2rem;
  color: #2c2b5e;
  font-weight: 800;
}

span {
  color: #202020;
  font-size: 1.5rem;
}

video, img {
  width: 100%;
  max-width: 55rem;
  border-radius: 15px;
}

button {
  border: none;
  padding: 1rem;
  border-radius: 1.5rem;
  color: #f0efef;
  background-color: #ec1c23;
  font-weight: 600;
  margin-top: 2rem;
}

input {
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #202020;
}

.glass {
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.11);
  padding: 1rem;
  margin: 1.5rem 0;
}

.mobile {
  display: block;
}

.desktop {
  display: none;
}

@media (min-width: 765px) {
  .mobile {
    display: none;
  }

  .desktop {
    display: block;
  }
}
.header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  height: 5rem;
}
.header .header--logo {
  display: flex;
  align-items: center;
  height: 100%;
  width: 7rem;
}
.header .header--logo img {
  width: 100%;
}

.header--texto--container {
  display: none;
}
.header--texto--container .header--texto {
  position: absolute;
  right: 0;
  top: 4rem;
  height: 100%;
  width: 100%;
  background-color: chocolate;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header--texto--container .header--texto ul {
  font-size: 2.5rem;
  text-align: center;
  display: flex;
  gap: 2rem;
  flex-direction: column;
  color: #f0efef;
  text-transform: uppercase;
  font-weight: bold;
}
.header--texto--container .header--texto ul li {
  cursor: pointer;
}
.header--texto--container .header--texto ul li:hover {
  color: #202020;
}

.header--iconos__hamburguer i {
  cursor: pointer;
}

.active {
  display: block;
}

@media (min-width: 765px) {
  .header {
    height: 7rem;
  }
  .header .header--logo {
    width: 10rem;
  }

  .header--texto--container {
    display: block;
    height: 100%;
    display: flex;
    align-items: center;
  }
  .header--texto--container .header--texto {
    background-color: transparent;
    position: sticky;
    top: 0;
    height: 100%;
  }
  .header--texto--container .header--texto ul {
    flex-direction: row;
    color: #202020;
  }
  .header--texto--container .header--texto ul li {
    font-size: 1.5rem;
  }

  .header--iconos__hamburguer {
    display: none;
  }
}
.front--div--img {
  position: relative;
  text-align: center;
  background-image: url("../img/front--div/mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  height: calc(100vh - 7rem);
}
.front--div--img .front--div--opacity {
  position: absolute;
  background-color: black;
  height: 100%;
  width: 100%;
  opacity: 0.7;
}
.front--div--img .front--div--textBox {
  position: absolute;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.front--div--img .front--div--textBox h1, .front--div--img .front--div--textBox p {
  color: #f0efef;
}
.front--div--img .front--div--textBox .textBox--h1 {
  font-size: 3rem;
}
.front--div--img .front--div--textBox .textBox--h1 span {
  font-size: 3rem;
  color: #ec1c23;
}
.front--div--img .front--div--textBox .textBox--btn button {
  width: 10rem;
  cursor: pointer;
}

@media (min-width: 765px) {
  .front--div--textBox {
    width: 100%;
  }
  .front--div--textBox h1, .front--div--textBox p {
    font-size: 4rem;
  }
  .front--div--textBox .textBox--btn button {
    font-size: 2rem;
    width: 25rem !important;
  }
}
.welcome .welcome--text span {
  color: #ec1c23;
  font-size: 2rem;
}
.welcome .welcome--button {
  margin-bottom: 2rem;
}
.welcome .welcome--button button {
  background-color: #ec1c23;
  color: #f0efef;
}
.welcome .welcome--img {
  display: flex;
  justify-content: center;
}
.welcome .welcome--img .welcome--mobile {
  display: block;
}
.welcome .welcome--img .welcome--desktop {
  display: none;
}

@media (min-width: 765px) {
  .welcome {
    display: flex;
    width: 70%;
    justify-content: center;
    align-items: center;
    margin: 3rem auto;
  }
  .welcome p {
    max-width: 100rem;
    font-size: 2rem;
  }

  .welcome--text {
    display: flex;
    flex-direction: column;
  }

  .welcome--img .welcome--mobile {
    display: none;
  }
  .welcome--img .welcome--desktop {
    display: block;
  }
}
.cursos {
  margin-bottom: 5rem;
}
.cursos .cursos--text {
  margin-top: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cursos--cards {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.cursos--cards .cursos--card .cursos--card--img {
  display: flex;
  justify-content: center;
}
.cursos--cards .cursos--card .cursos--card--img .curso--img--mobile {
  display: block;
}
.cursos--cards .cursos--card .cursos--card--img .curso--img--desktop {
  display: none;
}
.cursos--cards .cursos--card--button button {
  cursor: pointer;
}

@media (min-width: 765px) {
  .cursos {
    margin: 10rem 0;
  }

  .cursos--cards {
    display: flex;
  }

  .cursos--card {
    width: 20%;
  }
  .cursos--card .cursos--card--img .curso--img--mobile {
    display: none;
  }
  .cursos--card .cursos--card--img .curso--img--desktop {
    display: block;
  }
}
.video--tour {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5rem;
}
.video--tour .video--tour--text {
  margin-bottom: 2rem;
  text-align: center;
}

.blog .blog--text {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.blog--cards {
  display: flex;
  flex-wrap: wrap;
}

.blog--card .blog--card--date {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
}
.blog--card .blog--card--date span {
  padding: 0.5rem;
  border-radius: 10px;
}
.blog--card .blog--card--date .span1 {
  background-color: #2c2b5e;
  color: #f0efef;
}
.blog--card .blog--card--date .span2 {
  background-color: #ec1c23;
  color: #f0efef;
}
.blog--card .blog--card--date .span3 {
  background-color: #2c2b5e;
  color: #f0efef;
}
.blog--card .blog--card--img {
  display: flex;
  justify-content: center;
}

@media (min-width: 765px) {
  .blog--cards {
    display: flex;
    gap: 2rem;
    width: 80%;
    margin: 2rem auto;
    flex-wrap: wrap;
    justify-content: center;
  }

  .blog--card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: nowrap;
    width: 25%;
  }
}
.newsletter {
  margin-top: 5rem;
  background-color: #d6d6d6;
  border-radius: 10px;
  padding: 1rem;
}
.newsletter .newsletter--text {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.newsletter .newsletter--input--button {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
}
.newsletter button {
  cursor: pointer;
}

@media (min-width: 765px) {
  .newsletter {
    width: 60%;
    margin: 20rem auto;
    height: 50rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .newsletter .newsletter--text h3 {
    font-size: 3rem;
  }
  .newsletter .newsletter--input--button {
    width: 70%;
    margin: 0 auto;
  }
}
.last--text {
  margin-top: 2rem;
}
.last--text img {
  width: 25rem;
}
.last--text p {
  margin-bottom: 1rem;
}
.last--text .last--text--iconos {
  margin-top: 1rem;
  display: flex;
  gap: 1rem;
}
.last--text .last--text--iconos i {
  cursor: pointer;
  color: #ec1c23;
}
.last--text .last--text--iconos i:hover {
  color: #2c2b5e;
}

@media (min-width: 765px) {
  .last--text {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .last--text p {
    font-size: 2.5rem;
    text-align: center;
  }
}
.footer ul {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.copyright {
  background-color: #2c2b5e;
  color: #f0efef;
  font-size: 0.5rem;
  text-align: center;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.copyright p {
  font-size: 2rem;
}

@media (min-width: 765px) {
  .footer {
    width: 80%;
    margin: 12rem auto;
    display: flex;
    justify-content: space-evenly;
    gap: 1.5rem;
  }
}