.footer{
    ul{
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
}
.copyright{
    background-color: $fondo--morado;
    color: $color--fuente--blanca;
    font-size: .5rem;
    text-align: center;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    p{
        font-size: 2rem;
    }
}

@media (min-width:765px){
    .footer{
        width: 80%;
        margin: 12rem auto;
        display: flex;
        justify-content: space-evenly;
        gap: 1.5rem;
    }
}