.blog{
    .blog--text{
        display: flex;
        width: 100%;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}
.blog--cards{
    display: flex;
    flex-wrap: wrap;
}
.blog--card{
    .blog--card--date{
        display: flex;
        gap: 1rem;
        flex-wrap:wrap;
        justify-content: center;
        margin-top: 1rem;
        span{
            padding: .5rem;
            border-radius: 10px;
        }
        .span1{
            background-color: $fondo--morado;
            color: $color--fuente--blanca;
        }
        .span2{
            background-color: $naranja;
            color: $color--fuente--blanca;
        }
        .span3{
            background-color: $color--fuente--azul;
            color: $color--fuente--blanca;
        }
    }
    .blog--card--img{
        display: flex;
        justify-content: center;
    }
}

@media (min-width: 765px){
    .blog--cards{
        display: flex;
        gap: 2rem;
        width: 80%;
        margin: 2rem auto;
        flex-wrap: wrap;
        justify-content: center;
    }
    .blog--card{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: nowrap;
        width: 25%;
    }
}