html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *::before, *::after{
    box-sizing: inherit;
}
body{
    font-family: $fuente--principal;
    font-size: 1.5rem;
    color: $color--fuente;
    background-color:$fondo--principal;
    padding: 1rem;
    margin: 0;
}

p{
    font-size: 1.5rem;
    margin: 0;
    font-weight: 400;
}
i{
    color: $color--fuente--azul;
}
a{
    text-decoration: none;
    color: $color--fuente;
}
ul, li{
    text-decoration: none;
    padding: 0;
    list-style: none;
}

h1, h2, h3{
    font-size: 2rem;
    color: $color--fuente--azul;
    font-weight: 800;
}


span{
    color: $color--fuente;
    font-size: 1.5rem;
}
video, img{
    width: 100%;
    max-width: 55rem;
    border-radius: 15px;
}
button{
    border: none;
    padding: 1rem;
    border-radius: 1.5rem;
    color: $color--fuente--blanca;
    background-color: $naranja;
    font-weight: 600;
    margin-top: 2rem;
}

input{
    border: none;
    background-color: transparent;
    border-bottom: 2px solid $color--fuente;
}

.glass{
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 1px );
    -webkit-backdrop-filter: blur( 1px );
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.11);
    padding: 1rem;
    margin: 1.5rem 0;
}


.mobile{
    display: block;
}
.desktop{
    display: none;
}

@media (min-width: 765px) {
    .mobile{
        display: none;
    }
    .desktop{
        display: block;
    }

}