.header{
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    height: 5rem;
    .header--logo{
        display: flex;
        align-items: center;
        height: 100%;
        width: 7rem;
        img{
            width: 100%;
        }
    }
}


.header--texto--container{
    display: none;
    .header--texto{
    position: absolute;
    right: 0;
    top: 4rem;
    height: 100%;
    width: 100%;
    background-color: chocolate;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    ul{
        font-size: 2.5rem;
        text-align: center;
        display: flex;
        gap: 2rem;
        flex-direction: column;
        color: $color--fuente--blanca;
        text-transform: uppercase;
        font-weight: bold;
        li{
            cursor: pointer;
            &:hover{
                color: $color--fuente;
            }
        }
    }
    }
}
.header--iconos__hamburguer{
    i{
        cursor: pointer;
    }
}

.active{
    display: block;
}

@media (min-width: 765px){
    .header{
        height: 7rem;
        .header--logo{
            width: 10rem;
        }
    }
    .header--texto--container{
        display: block;
        height: 100%;
        display: flex;
        align-items: center;
        .header--texto{
            background-color: transparent;
            position: sticky;
            top: 0;
            height: 100%;
            ul{
                flex-direction: row;
                color: $color--fuente;
                li{            
                    font-size: 1.5rem;
                }
            }
        }
    }
    .header--iconos__hamburguer{
        display: none;
    }
}