.welcome{
    .welcome--text{
        span{
            color: $naranja;
            font-size: 2rem;
        }
    }
    .welcome--button{
        margin-bottom: 2rem;
        button{
            background-color: $naranja;
            color: $color--fuente--blanca;
        }
    }
    .welcome--img{
        display: flex;
        justify-content: center;
        .welcome--mobile{
            display: block;
        }
        .welcome--desktop{
            display: none;
        }
    }
}

@media (min-width:765px) {
    .welcome{
        display: flex;
        width: 70%;
        justify-content: center;
        align-items: center;
        margin: 3rem auto;
        p{
            max-width: 100rem;
            font-size: 2rem;
        }
    }
    .welcome--text{
        display: flex;
        flex-direction: column;
        
    }
    .welcome--img{
        .welcome--mobile{
            display: none;
        }
        .welcome--desktop{
            display: block;
        }
    }
}