.cursos{
    .cursos--text{
        margin-top: 2rem;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    margin-bottom: 5rem;
}

.cursos--cards{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    .cursos--card{
        .cursos--card--img{
            display: flex;
            justify-content: center;
            .curso--img--mobile{
                display: block;
            }
            .curso--img--desktop{
                display: none;
            }
        }
    }
    
    .cursos--card--button{
        button{
            cursor: pointer;
        }
    }
}

@media (min-width: 765px) {
    .cursos{
        margin: 10rem 0;
    }
    .cursos--cards{
        display: flex;
    }
    .cursos--card{
        width: 20%;
        .cursos--card--img{
            .curso--img--mobile{
                display: none;
            }
            .curso--img--desktop{
                display: block;
            }
        }

    }
}